/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #1a1b15;
  color: #FFFFFF;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 700;
  color: #FFFFFF;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

p {
  color: #FFFFFF;
  font-size: 2rem;
}

.container {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

.center-text {
  text-align: center;
}

.no-margin {
  margin: 0;
}

.alert {
  display: block;
  text-align: center;
  font-weight: 700;
  font-size: 1.8rem;
  margin: 2.5rem 0;
}
.alert.error {
  color: #cb0000;
}
.alert.success {
  color: #329f00;
}

.input-error {
  display: inline-block;
  color: #cb0000;
  font-size: 1.4rem;
  margin-top: 0.8rem;
}

.form {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  width: 70%;
  margin: 0 auto;
}
.form .label-input {
  font-size: 1.5rem;
}
.form .form-input {
  width: 100%;
  padding: 1.5rem;
  background-color: #e1e1e1;
  border: 1px solid transparent;
  margin-top: 1rem;
  -webkit-box-shadow: 0px 0px 6px 2px rgb(108, 108, 108);
  -moz-box-shadow: 0px 0px 6px 2px rgb(108, 108, 108);
  box-shadow: 0px 0px 6px 2px rgb(108, 108, 108);
}
.form .form-input:focus {
  border: 1px solid #1a1b15;
  outline: none;
}
.form .form-input:disabled {
  background-color: #6d7158;
  color: #FFFFFF;
  cursor: not-allowed;
}
.form .actions {
  width: 100%;
}
@media (min-width: 768px) {
  .form .actions {
    display: flex;
    gap: 1rem;
    align-items: center;
    justify-content: space-between;
  }
}
.form .submit-btn {
  background-color: #0da6f3;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.2s;
  text-align: center;
  display: block;
  width: 100%;
}
@media (min-width: 768px) {
  .form .submit-btn {
    width: auto;
  }
}
.form .submit-btn:hover {
  background-color: #0a85c3;
  cursor: pointer;
}
.form .delete-btn {
  background-color: #cb0000;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.2s;
  text-align: center;
  display: block;
  width: 100%;
}
@media (min-width: 768px) {
  .form .delete-btn {
    width: auto;
  }
}
.form .delete-btn:hover {
  background-color: #980000;
  cursor: pointer;
}

.all-services {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
  width: 100%;
}
@media (min-width: 768px) {
  .all-services {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.all-services .service {
  background-color: #FFFFFF;
  border-radius: 0.6rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 2rem 1rem;
  gap: 2rem;
  transition: transform 0.1s ease-in-out;
}
.all-services .service.selected {
  background-color: #0da6f3;
}
.all-services .service.selected .service-name {
  color: #FFFFFF;
}
.all-services .service.selected .service-price {
  color: #FFFFFF;
}
.all-services .service:hover {
  transform: scale(1.05);
  cursor: pointer;
}
.all-services .service .service-name {
  color: #1a1b15;
}
.all-services .service .service-price {
  color: #0da6f3;
  font-size: 3rem;
  font-weight: 700;
}

.tab {
  width: 100%;
}
.tab button {
  display: block;
  width: 100%;
  background-color: #0da6f3;
  padding: 1rem;
  color: #FFFFFF;
  text-transform: uppercase;
  font-weight: 700;
  border: none;
  margin-bottom: 1rem;
}
.tab button:last-of-type {
  margin-bottom: 0;
}
.tab button:hover {
  cursor: pointer;
  background-color: #0a85c3;
}
.tab button.active {
  background-color: #FFFFFF;
  color: #0da6f3;
}
@media (min-width: 768px) {
  .tab {
    display: flex;
  }
  .tab button {
    border-right: 1px solid #0a85c3;
    margin: 0;
  }
  .tab button:last-of-type {
    border: none;
  }
}

.pagination {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  gap: 1rem;
}
.pagination .btn {
  background-color: #0da6f3;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.2s;
  text-align: center;
  display: block;
  width: 100%;
}
@media (min-width: 768px) {
  .pagination .btn {
    width: auto;
  }
}
.pagination .btn:hover {
  background-color: #0a85c3;
  cursor: pointer;
}
.pagination .btn.btn-hide {
  visibility: hidden;
}

.user-nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 90%;
  margin: 0 auto;
}
.user-nav .logout-link {
  font-size: 1.8rem;
  color: #0da6f3;
}
.user-nav .logout-link:hover {
  text-decoration: underline;
}

.appointment {
  width: 100%;
  border-top: 1px solid #e1e1e1;
  margin: 3rem 0;
  padding: 1rem 0;
}
.appointment span {
  color: #0da6f3;
  font-weight: 700;
}
.appointment .btn-delete {
  margin: 0 auto;
  background-color: #cb0000;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.2s;
  text-align: center;
  display: block;
  width: 100%;
}
@media (min-width: 768px) {
  .appointment .btn-delete {
    width: auto;
  }
}
.appointment .btn-delete:hover {
  background-color: #980000;
  cursor: pointer;
}

.admin-navbar {
  width: 100%;
}
@media (min-width: 768px) {
  .admin-navbar {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    gap: 1rem;
  }
}
.admin-navbar .btn {
  background-color: #0da6f3;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.2s;
  text-align: center;
  display: block;
  width: 100%;
  font-size: 1.5rem;
}
@media (min-width: 768px) {
  .admin-navbar .btn {
    width: auto;
  }
}
.admin-navbar .btn:hover {
  background-color: #0a85c3;
  cursor: pointer;
}

.app-container {
  height: 100vh;
  display: grid;
  gap: 0rem;
  grid-template-columns: repeat(1, 1fr);
}
.app-container .app-image {
  display: none;
}
@media (min-width: 1024px) {
  .app-container {
    display: grid;
    gap: 0rem;
    grid-template-columns: repeat(2, 1fr);
  }
  .app-container .app-image {
    display: block;
  }
  .app-container .app-image img {
    position: sticky;
    top: 0;
    height: 100vh;
  }
}

.auth-section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 1.5rem 0;
}
.auth-section .name-inputs {
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .auth-section .name-inputs {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.auth-section .forgot-password {
  text-align: right;
  margin-top: 0.9rem;
}
.auth-section .forgot-password a {
  color: #e1e1e1;
  font-size: 1.4rem;
}
.auth-section .forgot-password a:hover {
  text-decoration: underline;
}
.auth-section .auth-helper {
  text-align: center;
}
.auth-section .auth-helper a {
  font-size: 1.8rem;
  color: #0da6f3;
}
.auth-section .auth-helper a:hover {
  text-decoration: underline;
}

.appointments-section {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  padding: 1.5rem 0;
}
.appointments-section .section-title {
  margin: 6rem 0;
  font-size: 5rem;
}

.appointment-step {
  display: none;
}
.appointment-step.show {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  margin: 5rem 0;
}

.services-summary {
  width: 100%;
  margin-bottom: 3rem;
}
.services-summary h2 {
  margin-bottom: 5rem;
}
.services-summary span {
  color: #0da6f3;
  font-size: 2.2rem;
  font-weight: 700;
}
.services-summary .service-summary {
  width: 100%;
  border-bottom: 1px solid #e1e1e1;
}
.services-summary .total-services {
  font-size: 2.5rem;
}

.appointment-summary {
  width: 100%;
}
.appointment-summary h2 {
  margin-bottom: 5rem;
}
.appointment-summary span {
  color: #0da6f3;
  font-size: 2.2rem;
  font-weight: 700;
}
.appointment-summary .send-btn {
  background-color: #0da6f3;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.2s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .appointment-summary .send-btn {
    width: auto;
  }
}
.appointment-summary .send-btn:hover {
  background-color: #0a85c3;
  cursor: pointer;
}

.admin-section {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  padding: 1.5rem 0;
}
.admin-section .section-title {
  margin: 3rem 0;
  font-size: 4rem;
}
.admin-section .not-found {
  margin: 5rem 0;
}