@use 'variables' as v;

h1, h2, h3 {
    font-family: v.$main-font;
    margin: 0 0 calc(v.$separation / 2) 0 ;
    font-weight: v.$bold;
    color: v.$white;
    text-align: center;
}

h1 {
    font-size: 4rem;
}

h2 {
    font-size: 3.6rem;
}

h3 {
    font-size: 3rem;
    text-align: center;
}

p {
    color: v.$white;
    font-size: 2rem;
}
